import { useState } from 'react';
import { useNavigate, Link } from 'react-router-dom';
import { authAPI } from '../services/api';
import { Activity, Mail, Lock, User, AlertCircle } from 'lucide-react';
import "./RegisterPage.css";

function RegisterPage({ setUser }) {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    email: '',
    password: '',
    name: ''
  });
  const [error, setError] = useState('');
  const [loading, setLoading] = useState(false);

  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');
    setLoading(true);

    try {
      const response = await authAPI.register(formData);
      const { access_token, user_id } = response.data;

      localStorage.setItem('access_token', access_token);

      //  []    ,
      //   formData  user   .
      const newUser = {
        user_id: user_id,
        email: formData.email,
        name: formData.name,
        //   body_condition  
        body_condition: { injured_parts: [], pain_level: 0, limitations: [] } 
      };
      setUser(newUser);
      
      // navigate('/login');
      navigate('/onboarding');
    } catch (err) {
      setError(err.response?.data?.detail || ' .');
    } finally {
      setLoading(false);
    }
  };

  // --- JSX     ---
  return (
    <>
      <div className="register-page-wrapper">
        <div className="register-container">
          <div className="header-section">
            <div className="logo-icon-wrapper">
              <Activity className="logo-icon" />
            </div>
            <h1 className="app-title">Fitner</h1>
            <p className="app-subtitle">AI    </p>
          </div>

          <div className="form-card">
            <h2 className="card-title"></h2>

            {error && (
              <div className="error-message-box">
                <AlertCircle className="error-icon" />
                <p>{error}</p>
              </div>
            )}

            <form onSubmit={handleSubmit} className="form-layout">
              <div className="input-group">
                <label className="input-label">
                  
                </label>
                <div className="input-wrapper">
                  <User className="input-icon" />
                  <input
                    type="text"
                    required
                    value={formData.name}
                    onChange={(e) => setFormData({ ...formData, name: e.target.value })}
                    className="input-field"
                    placeholder=""
                  />
                </div>
              </div>

              <div className="input-group">
                <label className="input-label">
                  
                </label>
                <div className="input-wrapper">
                  <Mail className="input-icon" />
                  <input
                    type="email"
                    required
                    value={formData.email}
                    onChange={(e) => setFormData({ ...formData, email: e.target.value })}
                    className="input-field"
                    placeholder="your@email.com"
                  />
                </div>
              </div>

              <div className="input-group">
                <label className="input-label">
                  
                </label>
                <div className="input-wrapper">
                  <Lock className="input-icon" />
                  <input
                    type="password"
                    required
                    minLength={6}
                    value={formData.password}
                    onChange={(e) => setFormData({ ...formData, password: e.target.value })}
                    className="input-field"
                    placeholder=" 6 "
                  />
                </div>
              </div>

              <button
                type="submit"
                disabled={loading}
                className={`submit-button ${loading ? 'button-disabled' : ''}`}
              >
                {loading ? ' ...' : ''}
              </button>
            </form>

            <div className="footer-link-section">
              <p className="footer-text">
                  ?{' '}
                <Link to="/login" className="login-link">
                  
                </Link>
              </p>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default RegisterPage;